/* ============================================
   ðŸŽ¨ PERFORMANCE OPTIMIZED HOVER ANIMATIONS
   Clean, Fast, Responsive
   ============================================ */

.project-card {
  display: flex;
  flex-direction: column;
  position: relative;
  /* Performance: Removed backdrop-filter */
  background: rgba(255, 255, 255, 0.95);
  padding: 2rem;
  border-radius: 16px;
  border: 1px solid rgba(229, 231, 235, 0.5);
  /* Simple border */
  justify-content: space-between;
  overflow: hidden;
  cursor: pointer;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  transition: transform 0.2s ease, box-shadow 0.2s ease, border-color 0.2s ease;
  /* Optimization: Only animate composite properties */
  will-change: transform;
  height: 300px;
  width: 100%;
}

/* Dark mode */
.dark-mode .project-card {
  background: #1e293b;
  /* Solid color for better perf */
  border: 1px solid #334155;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.2);
}

/* ============================================
     ðŸŽ¨ OPTIMIZED HOVER
     ============================================ */
.project-card:hover {
  transform: translateY(-4px);
  border-color: #6366f1;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.dark-mode .project-card:hover {
  border-color: #818cf8;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.3);
}

/* ============================================
     ðŸŽ¨ REPO NAME & DESCRIPTION
     ============================================ */
.repo-name-div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  min-height: 40px;
  position: relative;
  z-index: 2;
}

.repo-svg {
  margin-right: 0.5rem;
  min-width: 16px;
}

.project-card:hover .repo-svg {
  color: #6366f1;
}

.repo-name {
  white-space: nowrap;
  line-height: 1.2;
  text-overflow: ellipsis;
  color: rgb(36, 41, 46);
  font-family: "Google Sans Regular", sans-serif;
  font-size: 22px;
  font-weight: 700;
  letter-spacing: -0.5px;
  overflow: hidden;
  margin: 0;
  max-width: 80%;
  position: relative;
  transition: color 0.2s ease;
}

.dark-mode .repo-name {
  color: rgb(249, 250, 251);
}

.project-card:hover .repo-name {
  color: #6366f1;
}

/* Simple underline instead of animated gradient */
.repo-name::after {
  content: "";
  position: absolute;
  bottom: -4px;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #6366f1;
  transition: width 0.2s ease;
}

.project-card:hover .repo-name::after {
  width: 100%;
}

.repo-description {
  word-wrap: break-word;
  text-align: left;
  display: -webkit-box;
  font-family: "Google Sans Regular", sans-serif;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  font-size: 16px;
  line-height: 1.5;
  margin: 0.5rem 0;
  overflow: hidden;
  text-overflow: ellipsis;
  flex-grow: 1;
  min-height: 72px;
  max-height: 72px;
  color: #6b7280;
  position: relative;
  z-index: 2;
  transition: color 0.2s ease;
}

.dark-mode .repo-description {
  color: #9ca3af;
}

.project-card:hover .repo-description {
  color: #374151;
}

.dark-mode .project-card:hover .repo-description {
  color: #e5e7eb;
}

/* ============================================
     ðŸŽ¨ DETAILS SECTION
     ============================================ */
.repo-details {
  margin-top: auto;
  padding-top: 1rem;
  border-top: 1px solid rgba(229, 231, 235, 0.5);
  position: relative;
  z-index: 2;
  transition: border-color 0.2s ease;
}

.project-card:hover .repo-details {
  border-top-color: #6366f1;
}

.dark-mode .repo-details {
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

/* ============================================
     ðŸŽ¨ STATS ANIMATIONS (OPTIMIZED)
     ============================================ */
.repo-stats {
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  color: rgb(106, 115, 125);
  font-family: "Google Sans Regular", sans-serif;
}

.dark-mode .repo-stats {
  color: #9ca3af;
}

.repo-left-stat {
  flex-grow: 1;
  display: flex;
}

.repo-left-stat span {
  display: flex;
  align-items: center;
  margin-right: 0.75rem;
  transition: color 0.2s ease;
}

.project-card:hover .repo-left-stat span {
  color: #6366f1;
}

.language-color {
  width: 10px;
  height: 10px;
  background-color: #6366f1;
  margin-right: 0.25rem;
  border-radius: 50%;
}

.repo-star-svg {
  margin-right: 0.3rem;
}

/* ============================================
     ðŸŽ¨ LANGUAGE ICONS (OPTIMIZED)
     ============================================ */
.dev-icons-languages {
  display: flex;
  gap: 0.5rem;
  margin: 0;
  padding: 0;
  list-style: none;
}

.software-skill-inline-languages {
  transition: transform 0.2s ease, opacity 0.2s ease;
  opacity: 0.85;
}

.project-card:hover .software-skill-inline-languages {
  transform: translateY(-2px);
  opacity: 1;
}

/* ============================================
     ðŸŽ¨ LIVE BUTTON (OPTIMIZED)
     ============================================ */
.live-button {
  padding: 6px 14px;
  border-radius: 6px;
  border: 1px solid rgba(99, 102, 241, 0.4);
  font-size: 12px;
  font-weight: 600;
  font-family: "Google Sans Medium", sans-serif;
  cursor: pointer;
  /* Performance: Removed backdrop-filter */
  background: rgba(99, 102, 241, 0.1);
  color: #6366f1;
  display: flex;
  align-items: center;
  gap: 4px;
  margin-left: 10px;
  min-width: 50px;
  justify-content: center;
  transition: all 0.2s ease;
}

.dark-mode .live-button {
  background: rgba(99, 102, 241, 0.15);
  color: #818cf8;
}

.live-button:hover {
  background: rgba(99, 102, 241, 0.2);
  transform: translateY(-2px);
  border-color: #6366f1;
}

.live-button:active {
  transform: translateY(0);
}

.live-dot {
  width: 8px;
  height: 8px;
  background: #10b981;
  border-radius: 50%;
  position: relative;
}

/* Simplified Pulse */
.live-dot::after {
  content: "";
  position: absolute;
  inset: -3px;
  border: 2px solid #10b981;
  border-radius: 50%;
  opacity: 0.5;
}

/* ============================================
     ðŸŽ¨ RESPONSIVE
     ============================================ */
@media (max-width: 768px) {
  .project-card {
    height: auto;
    min-height: 280px;
    padding: 1.5rem;
  }
}

/* ============================================
     ðŸŽ¨ ACCESSIBILITY
     ============================================ */
@media (prefers-reduced-motion: reduce) {

  .project-card,
  .live-button,
  .repo-name::after,
  .software-skill-inline-languages {
    transition: none !important;
    animation: none !important;
    transform: none !important;
  }
}

/* ============================================
     ðŸŽ¨ PERFORMANCE OPTIMIZATIONS
     ============================================ */
.project-card,
.repo-svg,
.repo-name,
.repo-description,
.repo-details,
.software-skill-inline-languages,
.live-button {
  will-change: transform;
}

@media (hover: hover) {
  .project-card:not(:hover) {
    will-change: auto;
  }
}

/* ============================================
    PROJECT CARD STYLES
    ============================================ */

.project-card {
  position: relative;
  padding: 25px;
  border-radius: 15px;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.25, 1);
  display: flex;
  flex-direction: column;
  gap: 15px;
  border: 1px solid transparent;
  overflow: hidden;
}

.project-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 15px 40px rgba(0, 0, 0, 0.15) !important;
  border-color: rgba(100, 255, 218, 0.3);
}

/* Header */
.project-card-header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  gap: 10px;
}

.project-name {
  font-size: 18px;
  font-weight: 600;
  line-height: 1.4;
  margin: 0;
  flex: 1;
}

/* Description */
.project-description {
  font-size: 14px;
  line-height: 1.6;
  margin: 0;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  flex: 1;
}

/* Tech Stack Tags */
.project-tech-stack {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  margin-top: auto;
}

.tech-tag {
  padding: 4px 10px;
  border-radius: 15px;
  font-size: 11px;
  font-weight: 500;
  transition: all 0.2s ease;
}

.tech-tag:hover {
  transform: scale(1.05);
}

.tech-tag.more {
  background: transparent !important;
  font-style: italic;
}

/* ============================================
    ACTION BUTTONS
    ============================================ */

.project-actions {
  display: flex;
  gap: 10px;
  margin-top: 15px;
  padding-top: 15px;
  border-top: 1px solid rgba(128, 128, 128, 0.2);
}

.project-btn {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
  padding: 10px 16px;
  border: none;
  border-radius: 8px;
  font-size: 13px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

/* Live Button */
.live-btn {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  color: #ffffff;
  box-shadow: 0 4px 15px rgba(102, 126, 234, 0.3);
}

.live-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(102, 126, 234, 0.4);
}

/* Explore Button */
.explore-btn {
  background: linear-gradient(135deg, #11998e 0%, #38ef7d 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(17, 153, 142, 0.3);
}

.explore-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(17, 153, 142, 0.4);
}

/* Button Icons */
.project-btn svg {
  font-size: 14px;
}

/* Hint */
.project-hint {
  position: absolute;
  bottom: -30px;
  left: 50%;
  transform: translateX(-50%);
  font-size: 11px;
  color: #888;
  opacity: 0;
  transition: all 0.3s ease;
  white-space: nowrap;
}

.project-card:hover .project-hint {
  bottom: 8px;
  opacity: 1;
}

/* ============================================
    RESPONSIVE
    ============================================ */

@media (max-width: 768px) {
  .project-card {
    padding: 20px;
  }

  .project-name {
    font-size: 16px;
  }

  .project-description {
    font-size: 13px;
    -webkit-line-clamp: 2;
  }

  .project-actions {
    flex-direction: column;
    gap: 8px;
  }

  .project-btn {
    padding: 12px;
  }

  .project-hint {
    display: none;
  }
}

/* ============================================
    DARK/LIGHT THEME ADJUSTMENTS
    ============================================ */

/* Light Theme */
.light-theme .project-card {
  background: #0d0c0c;
  border: 1px solid #e5e7eb;
}

.light-theme .project-card:hover {
  border-color: #667eea;
}

/* Dark Theme */
.dark-theme .project-card {
  background: #1e1e1e;
  border: 1px solid #333;
}

.dark-theme .project-card:hover {
  border-color: #64ffda;
}

.live-dot {
  width: 8px;
  height: 8px;
  background: #10b981;
  border-radius: 50%;
  position: relative;
}

.live-dot::after {
  content: "";
  position: absolute;
  inset: -3px;
  border: 2px solid #10b981;
  border-radius: 50%;
  animation: ringPulse 1.5s ease-out infinite;
}

@keyframes ringPulse {
  0% {
    transform: scale(1);
    opacity: 0.8;
  }

  100% {
    transform: scale(1.8);
    opacity: 0;
  }
}